.App * {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.App {
  margin: 0;
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
  font-size: 14px;
  line-height: 2;
}

.App input {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

.App label {
  margin-right: 10px;
  margin-bottom: 5px;
}

.App label input {
  margin: .2rem;
}

.App .text_filter {
  width: 240pt;
  display: block;
}

.App .table_container {
  overflow-x: scroll;
}

.App .column {
  border: solid #3079ff;
  border-radius: 10px;
  width: 250px;
  margin: 5px;
  padding: 0 20px;
  height: 500px;
  overflow-y: scroll;
  white-space: pre-wrap;
  text-align: center;
}

.App .column h1 {
  text-align: center;
  margin: 16px;
  color: #3079ff;
  font-size: 14pt;
}

.App .column h2 {
  font-style: italic;
  font-size: 11pt;
}

.App .column p {
  font-style: italic;
  font-size: 9pt;
}

.App hr {
  margin: 20px;
}



